<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>changedEvent</td>
<td></td>
<td>Triggers when the underlying data changes.</td>
</tr>
<tr>
<td>errorEvent</td>
<td></td>
<td>Triggers if an error is encountered during processing.</td>
</tr>
<tr>
<td>loadingEvent</td>
<td></td>
<td>Triggers the data source either starts or stops loading.</td>
</tr>
<tr>
<td>clusterEvent</td>
<td>(clusteredEntities, cluster)</td>
<td>Gets the event that is fired when entities are added or removed from the collection.</td>
</tr>
<tr>
<td>collectionChanged</td>
<td>(collection, added, removed, changed)</td>
<td>Gets the event that will be raised when a new cluster will be displayed.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          show: true
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
        },
        subReady(cesiumInstance) {
          cesiumInstance.viewer.zoomTo(cesiumInstance.cesiumObject)
        }
      }
    }
  </script>